/* === mobile menu === */
let mobileBtn = document.querySelector(".nav__mobile");

mobileBtn.addEventListener("click", (e) => {
    /* mobileBtn.classList.toggle("nav__mobile-active") */
    if (mobileBtn.classList.toggle("nav__mobile--active")) {
        document.querySelector('.menu__mobile').classList.add("menu__mobile--active")
        document.querySelector('body').style.overflow ="hidden"
    } else {
        document.querySelector('.menu__mobile').classList.remove("menu__mobile--active")
        document.querySelector('body').style.overflow ="auto"
    }
})
/* === / mobile menu === */


/* === btn down === */
const linkBtnDown = document.querySelector('.link-down')

linkBtnDown.addEventListener('click', function (e) {
    e.preventDefault()

    const scrollPoint = linkBtnDown.getAttribute('href')

    document.querySelector(scrollPoint).scrollIntoView({
      behavior: 'smooth',
      block: 'start'
    })
})
/* === / btn down === */
/* === / header === */

/* === team slider === */
const swiper = new Swiper('.sl1der-team', {
    slidesPerView: 1,
    spaceBetween: 30,
    loop: true,
    effect: 'fade',
    fadeEffect: {
        crossFade: true
    },
    autoplay: {
        delay: 5000,
    },
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
    }
})


/* === / team slider === */

/* === booking === */
/* === library for date and time input === */
const fp = flatpickr("#date", {

    dateFormat: "Y-m-d"
})
const fpe = flatpickr("#time", {

    enableTime: true,
    noCalendar: true,
    dateFormat: "H:i",
    time_24hr: true
});
/* === / library for date and time input === */

/*  === getting form data === */
let buttonOpenPopup = document.querySelector(".form__button--booking") // open popup
let bookingPopupWrapper = document.querySelector(".booking-popup") // overlay with popup
let formData = {} // object with data form
let buttonClosePopup = document.querySelector(".booking-popup__close") // close popup
let popupContainer = document.querySelector(".booking-popup__key"); // container with add properties

// open popup and ad property
buttonOpenPopup.addEventListener("click", () => {
    let elements = document.getElementById("form-booking").elements; // all elements with form
    for (let i = 0; i < elements.length; i++) {
        let item = elements.item(i);
        formData[item.name] = item.value;
    }

    popupContainer.insertAdjacentHTML('afterbegin',
    `
    <ul class="booking-popup__key--property">
        <li>${formData.Name}</li>
        <li>${formData.Email}</li>
        <li>${formData.Phone}</li>
        <li>${formData.People}</li>
        <li>${formData.Data}</li>
        <li>${formData.Time}</li>
    </ul>
    `
    )
    bookingPopupWrapper.style.display = "block"; // open popup

    
})

// close popup and del property
buttonClosePopup.addEventListener("click", () => {
    bookingPopupWrapper.style.display = "none";
    let delProperty = document.querySelector('.booking-popup__key--property').remove()
})
/* console.log(obj) */  /*  === / getting form data === */

/* === / bookingr === */

